/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable @typescript-eslint/explicit-module-boundary-types */
import React, { useContext, useEffect, useState } from "react";
import Paper from "@material-ui/core/Paper";
import {
  EditingState,
  SortingState,
  IntegratedSorting,
  DataTypeProvider,
  SearchState,
  IntegratedFiltering,
} from "@devexpress/dx-react-grid";
import {
  Grid,
  TableHeaderRow,
  TableEditColumn,
  VirtualTable,
  Toolbar,
  SearchPanel,
  TableColumnVisibility,
  ColumnChooser,
} from "@devexpress/dx-react-grid-material-ui";
import { Command, Loading, PopupEditing } from "../../Shared";
import { getRowId } from "../../common";
import {
  createEvent,
  deleteEventById,
  getEvents,
  getLandingChartData,
  // getReminders,
  updateEvent,
} from "../../graphql";
import { useLazyQuery, useMutation } from "@apollo/client";
import {
  createdAtFormatter,
  currencyFormatter,
  doneFormatter,
  eventStatusFormatter,
  fromToFormatter,
  taskIdFormatter,
} from "../../Shared/colorFormat";
import PageLayout from "../main/PageLayout";
import { SearchTable } from "../../components";
import { getColumns } from "../../common/columns";
import PopupAppointment from "../../pubups/PopupAppointment";
import { EventsContext } from "../../contexts";
import DateNavigatorReports from "../../components/filters/DateNavigatorReports";
import {
  Box,
  Checkbox,
  colors,
  FormControlLabel,
  Typography,
  useMediaQuery,
} from "@material-ui/core";
import useTasks from "../../hooks/useTasks";
import getTasks from "../../graphql/query/getTasks";
import { Getter } from "@devexpress/dx-react-core";
import { TableComponent } from "../../Shared/TableComponent";

export default function Appointments({
  isRTL,
  words,
  menuitem,
  isEditor,
  theme,
  employees,
  departments,
  company,
  servicesproducts,
}) {
  const [due, setDue] = useState(false);
  const col = getColumns({ isRTL, words });
  const isMobile = useMediaQuery("(max-width:600px)");

  const [columns] = useState([
    col.startDate,
    col.fromto,
    col.customer,
    { id: 4, ref: "title", name: "title", title: words.title },
    col.docNo,
    col.taskId,
    col.employee,
    col.department,
    col.status,
    col.done,
    col.amount,
  ]);

  const [rows, setRows] = useState([]);
  const [loading, setLoading] = useState(false);
  const [start, setStart] = useState<any>(null);
  const [end, setEnd] = useState<any>(null);
  const [periodvalue, setPeriodvalue] = useState<any>(null);

  const { tasks } = useTasks();

  const {
    state: { currentDate, currentViewName, endDate, sort },
    dispatch,
  } = useContext(EventsContext);

  const currentViewNameChange = (e: any) => {
    dispatch({ type: "setCurrentViewName", payload: e.target.value });
  };
  const currentDateChange = (curDate: any) => {
    dispatch({ type: "setCurrentDate", payload: curDate });
  };
  const endDateChange = (curDate: any) => {
    dispatch({ type: "setEndDate", payload: curDate });
  };

  const [loadEvents, eventsData]: any = useLazyQuery(getEvents, {
    fetchPolicy: "cache-and-network",
  });

  const refresQuery = {
    refetchQueries: [
      {
        query: getEvents,
        variables: {
          start: start ? start.setHours(0, 0, 0, 0) : undefined,
          end: end ? end.setHours(23, 59, 59, 999) : undefined,
        },
      },
      {
        query: getLandingChartData,
      },
      {
        query: getTasks,
      },
    ],
  };

  useEffect(() => {
    const variables = {
      start: start ? start.setHours(0, 0, 0, 0) : undefined,
      end: end ? end.setHours(23, 59, 59, 999) : undefined,
      due,
    };
    loadEvents({
      variables,
    });
  }, [start, end, due]);

  const [addEvent] = useMutation(createEvent, refresQuery);
  const [editEvent] = useMutation(updateEvent, refresQuery);
  const [removeEventById] = useMutation(deleteEventById, refresQuery);

  const commitChanges = async ({ deleted }) => {
    if (deleted) {
      const _id = deleted[0];
      removeEventById({ variables: { _id } });
      setRows(rows.filter((row: any) => row._id !== _id));
    }
  };

  useEffect(() => {
    if (eventsData?.loading) {
      setLoading(true);
    }
    if (eventsData?.data?.getEvents?.data) {
      const { data } = eventsData.data.getEvents;
      setRows(data);
      setLoading(false);
    }
  }, [eventsData]);

  const refresh = () => {
    eventsData?.refetch();
  };

  const [tableColumnVisibilityColumnExtensions] = useState([
    { columnName: col.startDate.name, togglingEnabled: false },
    { columnName: col.fromto.name, togglingEnabled: false },
    { columnName: col.amount.name, togglingEnabled: false },
  ]);

  const setSortDispatch = (value: any) => {
    dispatch({ type: "setSort", payload: value });
  };
  return (
    <PageLayout
      menuitem={menuitem}
      isRTL={isRTL}
      words={words}
      isEditor={isEditor}
      theme={theme}
      refresh={refresh}
      periodvalue={periodvalue}
      setPeriodvalue={setPeriodvalue}
      bgcolor={colors.blue[500]}
    >
      <Paper>
        <Box
          display="flex"
          style={{
            position: "absolute",
            zIndex: 111,
            flexDirection: "row",
            alignItems: "center",
          }}
        >
          <Box
            style={{
              opacity: due ? 0.5 : undefined,
              pointerEvents: due ? "none" : undefined,
            }}
          >
            <DateNavigatorReports
              setStart={setStart}
              setEnd={setEnd}
              currentDate={currentDate}
              currentDateChange={currentDateChange}
              currentViewName={currentViewName}
              currentViewNameChange={currentViewNameChange}
              endDate={endDate}
              endDateChange={endDateChange}
              views={[1, 7, 30, 365, 1000]}
              isRTL={isRTL}
              words={words}
              theme={theme}
              color={colors.blue[700]}
              bgcolor={colors.blue[50]}
            ></DateNavigatorReports>
          </Box>
          <Box
            style={{
              padding: 0,
              margin: 0,
              backgroundColor: colors.blue[50],
              paddingRight: 10,
              marginLeft: 40,
              marginRight: 40,
              borderRadius: 5,
            }}
          >
            <FormControlLabel
              control={
                <Checkbox
                  style={{ padding: 7 }}
                  checked={due}
                  onChange={() => setDue(!due)}
                  color="primary"
                />
              }
              label={
                <Typography
                  style={{ color: colors.blue[700] }}
                  variant="subtitle2"
                >
                  {isRTL ? " " : "Due Appointments"}
                </Typography>
              }
              style={{ fontSize: 14 }}
            />
          </Box>
        </Box>
        <Grid rows={rows} columns={columns} getRowId={getRowId}>
          <SortingState
            defaultSorting={sort}
            onSortingChange={(srt: any) => setSortDispatch(srt)}
          />
          <EditingState onCommitChanges={commitChanges} />
          {!isMobile && <SearchState />}

          <IntegratedSorting />
          {!isMobile && <IntegratedFiltering />}

          <VirtualTable
            height={window.innerHeight - 133}
            messages={{
              noData: isRTL ? "  " : "no data",
            }}
            estimatedRowHeight={30}
            tableComponent={TableComponent}
          />
          <TableHeaderRow showSortingControls />
          <TableColumnVisibility
            columnExtensions={tableColumnVisibilityColumnExtensions}
          />
          <DataTypeProvider
            for={["fromto"]}
            formatterComponent={fromToFormatter}
          ></DataTypeProvider>
          <DataTypeProvider
            for={["startDate"]}
            formatterComponent={createdAtFormatter}
          ></DataTypeProvider>
          <DataTypeProvider
            for={["status"]}
            formatterComponent={eventStatusFormatter}
          ></DataTypeProvider>
          <DataTypeProvider
            for={["amount"]}
            formatterComponent={currencyFormatter}
          ></DataTypeProvider>
          <DataTypeProvider
            for={["done"]}
            formatterComponent={(props: any) =>
              doneFormatter({ ...props, editEvent })
            }
          ></DataTypeProvider>
          <DataTypeProvider
            for={["taskId"]}
            formatterComponent={(props: any) =>
              taskIdFormatter({ ...props, tasks })
            }
          ></DataTypeProvider>

          {isEditor && (
            <TableEditColumn
              showEditCommand
              showDeleteCommand
              showAddCommand
              commandComponent={Command}
            ></TableEditColumn>
          )}

          {!isMobile && <Toolbar />}
          <ColumnChooser />
          {!isMobile && (
            <SearchPanel
              inputComponent={(props: any) => {
                return <SearchTable isRTL={isRTL} {...props}></SearchTable>;
              }}
            />
          )}
          <PopupEditing addAction={addEvent} editAction={editEvent}>
            <PopupAppointment
              employees={employees}
              departments={departments}
              company={company}
              servicesproducts={servicesproducts}
              theme={theme}
              tasks={tasks}
              isEditor={isEditor}
            ></PopupAppointment>
          </PopupEditing>
          <Getter
            name="tableColumns"
            computed={({ tableColumns }) => {
              const result = [
                {
                  key: "editCommand",
                  type: TableEditColumn.COLUMN_TYPE,
                  width: 110,
                },
                ...tableColumns.filter(
                  (c: any) => c.type !== TableEditColumn.COLUMN_TYPE
                ),
              ];
              return result;
            }}
          />
        </Grid>
        {loading && <Loading isRTL={isRTL} />}
      </Paper>
    </PageLayout>
  );
}
